import { useEffect, useRef, useState } from "react";
import "../../App.scss"
import cw from "./cw.png"
import close from "./cc.png";
import closeb from "./cbb.png";
import plusw from "./plus.png"
import da from "./da.png";
import dab from "./dab.png";
import types from "./types.json";


function Todo({ theme }) {

    const [name, setName] = useState("");
    const [desc, setDesc] = useState("");
    const [catSrc, setCatSrc] = useState("");
    const dropdownRef = useRef(null);
    const [isOpen, setIsOpen] = useState(false);
    const [catB, setCatB] = useState("")
    const [catColor, setCatColor] = useState("")
    const [catValue, setCatValue] = useState("Select Category");
    const [dateT, setDateT] = useState("")
    const [storedTodos, setStoredTodos] = useState(JSON.parse(localStorage.getItem("todos")));
    const openCats = () => {
        setIsOpen(!isOpen);
    };


    const openFavs = () => {
        document.getElementById("add-todos").style.display = "flex";
    };

    const closeFavs = () => {
        document.getElementById("add-todos").style.display = "none";
        setCatValue('Select Category')
    };
    const favRef = useRef(null)

    useEffect(() => {
        function handleClickOutside(event) {
            if (favRef.current && !favRef.current.contains(event.target)) {
                closeFavs()
            }
        }

        document.addEventListener("mousedown", handleClickOutside);
        return () => {
            document.removeEventListener("mousedown", handleClickOutside);
        };
    }, []);

    useEffect(() => {
        function handleClickOutside(event) {
            if (dropdownRef.current && !dropdownRef.current.contains(event.target)) {
                setIsOpen(false);
            }
        }

        document.addEventListener("mousedown", handleClickOutside);
        return () => {
            document.removeEventListener("mousedown", handleClickOutside);
        };
    }, []);

    const onSubmitFavs = async (e) => {
        e.preventDefault();



        let existingArray = localStorage.getItem("todos");
        existingArray = existingArray ? JSON.parse(existingArray) : [];

        const generateRandomId = () => {
            return Math.random().toString(36).substr(2, 9);
        };

        const isUniqueId = (id) => {
            return existingArray.every((item) => item.id !== id);
        };

        let id = generateRandomId();
        while (!isUniqueId(id)) {
            id = generateRandomId();
        }

        existingArray.push({ id: id, name: name, desc: desc, image: catSrc, catValue: catValue, catColor: catColor, catB: catB, dateT: dateT });

        let newArrayJSON = JSON.stringify(existingArray);
        await localStorage.setItem("todos", newArrayJSON);
        await setStoredTodos(JSON.parse(localStorage.getItem("todos")));
        setDesc("");
        setName("");
        closeFavs();
        setCatSrc("");
        setDateT("")
        setCatValue("Select Category");
    };



    return (
        <>
            <div id="add-todos" className="add-favs">
                <div ref={favRef} className={theme == "light" ? "add-favs-box add-favs-box-light" : "add-favs-box"}>
                    <div className="add-favs-head">
                        <p className={theme == "light" ? "head-add head-add-light" : "head-add"}>Add todo</p>
                        <img className="close" onClick={closeFavs} src={theme == "light" ? closeb : close} alt="close" />
                    </div>
                    <form className="add-fav-form" onSubmit={onSubmitFavs} >

                        <div className={theme == "light" ? "dropdown dropdown-light" : "dropdown"} ref={dropdownRef}>
                            <div onClick={openCats} className={theme == "light" ? "drpbox drpbox-light" : "drpbox"}>
                                <p className={theme == "light" ? "dropbtn dropbtn-light" : "dropbtn"}>{catValue}</p>
                                <img className={isOpen ? "da daOpen" : "da"} src={theme == "light" ? dab : da} alt="da" />
                            </div>
                            {isOpen && (
                                <div className={theme == "light" ? "dropdown-content dropdown-content-light" : "dropdown-content"}>
                                    {/* <div className="search-box">
                                        <input
                                            className={theme == "light" ? "drpcont drpcont-light searchType searchType-light" : "drpcont searchType"}
                                            type="text"
                                            value={searchValue}
                                            onChange={(e) => setSearchValue(e.target.value)}
                                            placeholder="Search type here.."
                                        />
                                    </div> */}
                                    {
                                        isOpen ?
                                            types.map((ct, index) => (
                                                <div className={theme == "light" ? "dropcont drpcont-light" : "drpcont"}
                                                    onClick={() => {
                                                        setCatValue(ct.name);
                                                        setIsOpen(false);
                                                        setCatSrc(ct.src);
                                                        setCatB(ct.colorB)
                                                        setCatColor(ct.color)
                                                    }}
                                                    style={{ backgroundColor: `${ct.colorB}`, color: `${ct.color}` }}>
                                                    <img className="timg" src={require(`.${ct.src}`)} />
                                                    <p
                                                        style={{ color: `${ct.color}` }}


                                                        key={index}
                                                    >
                                                        {ct.name}
                                                    </p>
                                                </div>
                                            ))
                                            : null
                                    }
                                </div>
                            )}
                        </div>
                        <input required type="text" className={theme == "light" ? "add-fav-inp add-fav-inp-light" : "add-fav-inp"} placeholder="Todo Name" value={name} onChange={(e) => setName(e.target.value)} />
                        <input type="text" className={theme == "light" ? "add-fav-inp add-fav-inp-light" : "add-fav-inp"} placeholder="Todo description (optional)" value={desc} onChange={(e) => setDesc(e.target.value)} />

                        <input type="date" className={theme == "light" ? "add-fav-inp add-fav-inp-light date-inp" : "add-fav-inp date-inp"} placeholder="Todo deadline (optional)" value={dateT} onChange={(e) => setDateT(e.target.value)} />

                        <input type="submit" className="add-fav-sub" value="Add todo" />
                    </form>
                </div >
            </div >
            <div className="Todo">
                <div className="todo-box">
                    <div className="todo-box-head">
                        <p className="head">Your ToDos</p>
                        <img className="plusTodo" onClick={openFavs} src={plusw} />

                    </div>
                    <div className="todo-box-lower">
                        {
                            storedTodos && storedTodos.map((st) => (
                                <div className="todo">
                                    <div className="todo-head">
                                        <p className="todo-name">{st.name}</p>
                                    </div>
                                    {
                                        st.desc != "" || st.catValue != "Select Category"
                                            ?
                                            <div className="todo-cont">
                                                {
                                                    st.desc != "" && st.desc ?
                                                        <p className="todo-desc">
                                                            {st.desc}
                                                        </p>
                                                        : null
                                                }
                                                {
                                                     st.dateT != "" 
                                                        ? <p className="todo-desc">Deadline : {st.dateT}</p>
                                                        : null
                                                }
                                                {
                                                    st.catValue != "Select Category" && st.catValue
                                                        ?
                                                        <div className="todoCat" style={{ backgroundColor: `${st.catB}` }} >
                                                            <img className="tlimg" src={require(`.${st.image}`)} />
                                                            <p className="catName" style={{color : `${st.catColor}`}} >{st.catValue}</p>
                                                        </div>
                                                        : null
                                                }
                                            </div>
                                            : null
                                    }
                                </div>
                            ))
                        }
                    </div>
                </div>
            </div>
        </>

    )
}

export default Todo;