import { useEffect, useState } from "react";
import "../../App.scss"
import axios from "axios";

function Time({ timeD, dateD, weatherD, theme }) {


    const [weatherData, setWeatherData] = useState(null);
    const [loading, setLoading] = useState(true);
    const [cityName, setCityName] = useState(localStorage.getItem("city"))
    const [cityInp, setCityinp] = useState("")
    const [latitude, setLatitude] = useState('');
    const [longitude, setLongitude] = useState('');
    const [city, setCity] = useState('');
    const [error, setError] = useState(null);

    useEffect(() => {
        const getLocation = () => {
            if (navigator.geolocation) {
                navigator.geolocation.getCurrentPosition(
                    (position) => {
                        // console.log('Got position:', position);
                        setLatitude(position.coords.latitude);
                        setLongitude(position.coords.longitude);
                    },
                    (error) => {
                        // console.error('Error getting geolocation:', error);
                        setError('Error getting geolocation. Please make sure location services are enabled.');
                    }
                );
            } else {
                setError('Geolocation is not supported by this browser.');
            }
        };

        getLocation();
    }, []); // Empty dependency array means this effect runs once when component mounts

    useEffect(() => {
        const handleSearch = async () => {
            // alert("we ask you for location to show you weather of your city")
            try {

                const response = await axios.get(`https://nominatim.openstreetmap.org/reverse?lat=${latitude}&lon=${longitude}&format=json`);
                const data = response.data;
                setCity(data.address.state);
            } catch (error) {
                // console.error('Error fetching city:', error);
                setError('Error fetching city. Please try again.');
            }
        };

        if (latitude && longitude) {
            handleSearch();
        }
    }, [latitude, longitude]);












    const fetchWeatherData = async () => {
        try {
            const response = await axios.get(
                `https://api.openweathermap.org/data/2.5/weather?q=${city}&appid=107dccd9d6995860e0c29c13e619e243&units=metric`
            );
            setWeatherData(response.data);
            setLoading(false);
        } catch (error) {
            console.error('Error fetching weather data:', error);
        }
    };

    useEffect(() => {
        fetchWeatherData();
    }, [city])


    const [time, setTime] = useState(new Date()); // State to hold the current time

    useEffect(() => {
        const intervalID = setInterval(() => {
            // Update the time every second
            setTime(new Date());
        }, 1000);

        // Clear the interval on component unmount to avoid memory leaks
        return () => clearInterval(intervalID);
    }, []);

    let hours = time.getHours()
    let minutes = time.getMinutes();
    const day = time.getDay();
    const daysOfWeek = ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'];
    const dayName = daysOfWeek[day];
    const monthsShortForm = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];
    const month = time.getMonth()
    const monthName = monthsShortForm[month];
    const date = time.getDate();

    const year = time.getFullYear()

    let period = "AM"; // Default to AM

    // Convert to 12-hour format
    if (hours > 12) {
        hours -= 12;
        period = "PM";
    }

    // Handle midnight
    if (hours === 0) {
        hours = 12; 
    }

    hours = hours.toString().padStart(2, '0');
    minutes = minutes.toString().padStart(2, '0');




    return (
        <div className="Time">
            <div className={theme == "light" ? "time-box time-box-light" : theme == "custom" ? "time-box customcss" : "time-box"}>
                {

                    timeD == "show" ? <p className={theme == "light" ? " time-text time-text-light" : "time-text"} >{hours} : {minutes}<span className={theme == "light" ? "period period-light" : "period"} >{period}</span> </p> : null
                }
                {
                    dateD == "show" ? <p className={theme == "light" ? "day-name day-name-light" : "day-name"} > {dayName}, {date} {monthName} {year} </p> : null
                }
                {
                    weatherD == "show" && city !== '' ?
                        <p className='day-name' > {loading ? (

                            <div className='city-input-box'>
                                <p className={theme == "light" ? "day-name day-name-light" : "day-name"}>Loading weather..</p>
                            </div>

                        ) : (
                            <div className='temp-line' >
                                <p className={theme == "light" ? "day-name day-name-light" : "day-name"}>{city}</p>
                                <p className={theme == "light" ? "day-name day-name-light" : "day-name"}>{weatherData.main.temp}C</p>
                                {/* <img className='check' onClick={() => { localStorage.removeItem("city"); setCityName(""); fetchWeatherData(); setLoading(true) }} src={check} /> */}

                            </div>
                        )}</p>
                        : null
                }
            </div>
        </div>
    )
}

export default Time