import React, { useEffect, useRef, useState } from "react";
import "../../App.scss";
import webb from "./web2.png";
import plus from "./plus.png";
import plusb from "./pbb.png";
import close from "./cc.png";
import closeb from "./cbb.png";
import penc from "./penc.png";
import pencb from "./pencb.png";
import da from "./da.png";
import dab from "./dab.png";
import edits from "./types.json"
import types from "./types.json";
import { useLocation } from "react-router-dom";

function Favs({ theme }) {
    const [name, setName] = useState("");
    const [url, setUrl] = useState("");
    const [searchValue, setSearchValue] = useState("");
    const [storedFavorites, setStoredFavourites] = useState(JSON.parse(localStorage.getItem("favs")));
    const [catValue, setCatValue] = useState("Select type");
    const [catSrc, setCatSrc] = useState("");
    const [editCatValue, setEditCatValue] = useState("Select type");
    const [editCatSrc, setEditCatSrc] = useState("");
    const [fid, setFid] = useState("");

    const [isOpen, setIsOpen] = useState(false);
    const [isEditOpen, setIsEditOpen] = useState(false);

    const dropdownRef = useRef(null);
    const editRef = useRef(null);

    const openCats = () => {
        setIsOpen(!isOpen);
    };

    const openEditCats = () => {
        setIsEditOpen(!isEditOpen);
    };


    const favRef = useRef(null)

    useEffect(() => {
        function handleClickOutside(event) {
            if (favRef.current && !favRef.current.contains(event.target)) {
                closeFavs()
            }
        }

        document.addEventListener("mousedown", handleClickOutside);
        return () => {
            document.removeEventListener("mousedown", handleClickOutside);
        };
    }, []);

    const editBRef = useRef(null)

    useEffect(() => {
        function handleClickOutside(event) {
            if (editBRef.current && !editBRef.current.contains(event.target)) {
                editCFavs()
            }
        }

        document.addEventListener("mousedown", handleClickOutside);
        return () => {
            document.removeEventListener("mousedown", handleClickOutside);
        };
    }, []);



    useEffect(() => {
        function handleClickOutside(event) {
            if (dropdownRef.current && !dropdownRef.current.contains(event.target)) {
                setIsOpen(false);
            }
        }

        document.addEventListener("mousedown", handleClickOutside);
        return () => {
            document.removeEventListener("mousedown", handleClickOutside);
        };
    }, []);
    useEffect(() => {
        function handleClickOutsideEdit(event) {
            if (editRef.current && !editRef.current.contains(event.target)) {
                setIsEditOpen(false);
            }
        }

        document.addEventListener("mousedown", handleClickOutsideEdit);
        return () => {
            document.removeEventListener("mousedown", handleClickOutsideEdit);
        };
    }, []);


    const openFavs = () => {
        document.getElementById("add-favs").style.display = "flex";
    };

    const closeFavs = () => {
        document.getElementById("add-favs").style.display = "none";
    };

    const editOFavs = (id, name, url, catValue, catSrc) => {
        setEditCatValue(catValue);
        setEditCatSrc(catSrc);
        setName(name);
        setUrl(url);
        setFid(id);
        document.getElementById("edit-favs").style.display = "flex";
    };

    const editCFavs = () => {
        document.getElementById("edit-favs").style.display = "none";
    };

    const onEditFavs = async (e) => {
        e.preventDefault();

        let existingArray = localStorage.getItem("favs");
        existingArray = existingArray ? JSON.parse(existingArray) : [];

        const index = existingArray.findIndex((item) => item.id === fid);
        if (index !== -1) {
            existingArray[index] = { id: fid, name: name, url: url, logo: editCatSrc, catValue: editCatValue };
        }

        let newArrayJSON = JSON.stringify(existingArray);
        await localStorage.setItem("favs", newArrayJSON);
        await setStoredFavourites(JSON.parse(localStorage.getItem("favs")));
        setUrl("");
        setName("");
        editCFavs();
        setCatSrc("");
        setCatValue("Select Type");
    };
    const onDelFav = async (e) => {
        e.preventDefault();

        let existingArray = localStorage.getItem("favs");
        existingArray = existingArray ? JSON.parse(existingArray) : [];

        const index = existingArray.findIndex((item) => item.id === fid);
        if (index !== -1) {
            existingArray.splice(index, 1);
        }

        let newArrayJSON = JSON.stringify(existingArray);
        await localStorage.setItem("favs", newArrayJSON);
        await setStoredFavourites(JSON.parse(localStorage.getItem("favs")));
        setUrl("");
        setName("");
        editCFavs();
        setCatSrc("");
        setCatValue("Select Type");
    };

    const onSubmitFavs = async (e) => {
        e.preventDefault();

        let imgSrcL = `https://t0.gstatic.com/faviconV2?client=SOCIAL&type=FAVICON&fallback_opts=TYPE,SIZE,URL&url=${url}&size=256`

        let existingArray = localStorage.getItem("favs");
        existingArray = existingArray ? JSON.parse(existingArray) : [];

        const generateRandomId = () => {
            return Math.random().toString(36).substr(2, 9);
        };

        const isUniqueId = (id) => {
            return existingArray.every((item) => item.id !== id);
        };

        let id = generateRandomId();
        while (!isUniqueId(id)) {
            id = generateRandomId();
        }

        existingArray.push({ id: id, name: name, url: url, logo: imgSrcL, catValue: catValue });

        let newArrayJSON = JSON.stringify(existingArray);
        await localStorage.setItem("favs", newArrayJSON);
        await setStoredFavourites(JSON.parse(localStorage.getItem("favs")));
        setUrl("");
        setName("");
        closeFavs();
        setCatSrc("");
        setCatValue("Select Type");
    };

    // console.log(name, url, catValue, catSrc)
    const [mostVisitedUrls, setMostVisitedUrls] = useState([]);
    const location = useLocation();

    useEffect(() => {
        const visitedUrls = JSON.parse(localStorage.getItem('visitedUrls')) || {};

        const currentUrl = location.pathname + location.search;
        visitedUrls[currentUrl] = visitedUrls[currentUrl] ? visitedUrls[currentUrl] + 1 : 1;

        localStorage.setItem('visitedUrls', JSON.stringify(visitedUrls));

        const sortedUrls = Object.entries(visitedUrls)
            .sort(([, countA], [, countB]) => countB - countA)
            .slice(0, 5)
            .map(([url]) => url);

        setMostVisitedUrls(sortedUrls);
    }, [location]);

    console.log(mostVisitedUrls)

    return (
        <>
            <div id="edit-favs" className="add-favs">
                <div ref={editBRef} className={theme == "light" ? "add-favs-box add-favs-box-light" : "add-favs-box"}>
                    <div className="add-favs-head">
                        <p className={theme == "light" ? "head-add head-add-light" : "head-add"}>Edit site</p>
                        <img className="close" onClick={editCFavs} src={theme == "light" ? closeb : close} alt="close" />
                    </div>
                    <form onSubmit={onEditFavs} className="add-fav-form">


                        <input required type="text" className={theme == "light" ? "add-fav-inp add-fav-inp-light" : "add-fav-inp"} placeholder="Website Name" value={name} onChange={(e) => setName(e.target.value)} />
                        <input required type="text" className={theme == "light" ? "add-fav-inp add-fav-inp-light" : "add-fav-inp"} placeholder="Website URL" value={url} onChange={(e) => setUrl(e.target.value)} />
                        <input type="submit" className="add-fav-sub" value="Update site" />
                        <button onClick={onDelFav} className="add-fav-sub dbutton">
                            Delete site
                        </button>
                    </form>
                </div>
            </div>

            <div id="add-favs" className="add-favs">
                <div ref={favRef} className={theme == "light" ? "add-favs-box add-favs-box-light" : "add-favs-box"}>
                    <div className="add-favs-head">
                        <p className={theme == "light" ? "head-add head-add-light" : "head-add"}>Add site</p>
                        <img className="close" onClick={closeFavs} src={theme == "light" ? closeb : close} alt="close" />
                    </div>
                    <form onSubmit={onSubmitFavs} className="add-fav-form">
                        <p className="note note-light">Note: If you don't find your type, select "Select Type"</p>
                        {/* <div className={theme == "light" ? "dropdown dropdown-light" : "dropdown"} ref={dropdownRef}>
                            <div onClick={openCats} className={theme == "light" ? "drpbox drpbox-light" : "drpbox"}>
                                <p className={theme == "light" ? "dropbtn dropbtn-light" : "dropbtn"}>{catValue}</p>
                                <img className={isOpen ? "da daOpen" : "da"} src={theme == "light" ? dab : da} alt="da" />
                            </div>
                            {isOpen && (
                                <div className={theme == "light" ? "dropdown-content dropdown-content-light" : "dropdown-content"}>
                                    <div className="search-box">
                                        <input
                                            className={theme == "light" ? "drpcont drpcont-light searchType searchType-light" : "drpcont searchType"}
                                            type="text"
                                            value={searchValue}
                                            onChange={(e) => setSearchValue(e.target.value)}
                                            placeholder="Search type here.."
                                        />
                                    </div>
                                    {searchValue === ""
                                        ? types.map((ct, index) => (
                                            <p
                                                className={theme == "light" ? "dropcont drpcont-light" : "drpcont"}
                                                onClick={() => {
                                                    setCatValue(ct.name);
                                                    setIsOpen(false);
                                                    setCatSrc(ct.src);
                                                    setSearchValue("");
                                                }}
                                                key={index}
                                            >
                                                {ct.name}
                                            </p>
                                        ))
                                        : types.some((ct) => ct.name.toLowerCase().includes(searchValue.toLowerCase()))
                                            ? types.map((ct, index) =>
                                                ct.name.toLowerCase().includes(searchValue.toLowerCase()) ? (
                                                    <p
                                                        className={theme == "light" ? "drpcont drpcont-light" : "drpcont"}
                                                        onClick={() => {
                                                            setCatValue(ct.name);
                                                            setIsOpen(false);
                                                            setCatSrc(ct.src);
                                                            setSearchValue("");
                                                        }}
                                                        key={index}
                                                    >
                                                        {ct.name}
                                                    </p>
                                                ) : null
                                            )
                                            : <p className={theme == "light" ? "drpcont drpcont-light" : "drpcont"}>Not found</p>}
                                </div>
                            )}
                        </div> */}
                        <input required type="text" className={theme == "light" ? "add-fav-inp add-fav-inp-light" : "add-fav-inp"} placeholder="Website Name" value={name} onChange={(e) => setName(e.target.value)} />
                        <input required type="text" className={theme == "light" ? "add-fav-inp add-fav-inp-light" : "add-fav-inp"} placeholder="Website URL" value={url} onChange={(e) => setUrl(e.target.value)} />
                        <input type="submit" className="add-fav-sub" value="Add site" />
                    </form>
                </div>
            </div>

            <div className="Favs">
                <div className="fav-box">
                    {storedFavorites &&
                        storedFavorites.map((sf) => (
                            <div className="fav-show" key={sf.id}>
                                <a href={sf.url}>
                                    <div name={sf.name} className="fav" data-tip={sf.name}>
                                        <div className={theme == "light" ? "fav-img-box fav-img-box-light" : theme == "custom" ? "fav-img-box customcss" : "fav-img-box"}>
                                            <img className="fav-img" src={sf.logo ? sf.logo : webb} alt="logo" />
                                        </div>
                                        <p name={sf.name} data-tip={sf.name} className={theme == "light" ? "fav-name fav-name-light" : "fav-name"}>
                                            {sf.name}
                                        </p>
                                    </div>
                                </a>
                                <img className="penc" onClick={() => editOFavs(sf.id, sf.name, sf.url, sf.catValue, sf.logo)} src={theme == "light" ? pencb : penc} alt="edit" />
                            </div>
                        ))}
                    {storedFavorites && storedFavorites.length > 9 ? null : (
                        <div onClick={openFavs} className="fav">
                            <div className={theme == "light" ? "fav-img-box fav-img-box-light" : theme == "custom" ? "fav-img-box customcss" : "fav-img-box"}>
                                <img className="fav-img pi" src={theme == "light" ? plusb : plus} alt="plus" />
                            </div>
                            <p className={theme == "light" ? "fav-name fav-name-light" : "fav-name"} >Add site</p>
                        </div>
                    )}
                </div>
                {/* <div className="fav-box">
                    {
                        mostVisitedUrls && mostVisitedUrls.map((m) => (
                            <div className="fav-show">
                                <a href={m.url}>
                                    <div className="fav" >
                                        <div className={theme == "light" ? "fav-img-box fav-img-box-light" : theme == "custom" ? "fav-img-box customcss" : "fav-img-box"}>
                                            <img className="fav-img" src={`https://t0.gstatic.com/faviconV2?client=SOCIAL&type=FAVICON&fallback_opts=TYPE,SIZE,URL&url=${m.url}&size=256`} alt="logo" />
                                        </div>

                                    </div>
                                </a>

                            </div>
                        ))
                    }

                </div> */}
            </div>
        </>
    );
}

export default Favs;
